import React from 'react'
import emailjs from 'emailjs-com'
import styled from 'styled-components'
import Socialls from '../Socials/Socials'

const StyledContact = styled.div`
display: flex;
flex-direction: column;
align-items: center;
background-color: #3D2A35;
height: calc(100vh - 160px);
padding-top: 20px;
padding-bottom: 70px;
@media (max-width: 700px) {
    height: fit-content;
}
`
const ContactTitle = styled.div`
font-family: 'Staatliches', cursive;
width: 100%;
text-align: center;
h2 {
    color: #D70170;
    font-size: 1.8rem;
}

h4 {
    color: #FF859E;
    font-family: 'Questrial', sans-serif; 
    font-size: 19px;
}
`

const ContactContent = styled.div`
display: flex;
background-color: #734967;
box-shadow: -1px -1px 5px 5px #FF859E;
font-family: 'Questrial', sans-serif;
color: #FFB7CB;
align-items: center;
height: fit-content;
border-radius: 5px;

@media (max-width: 700px) {
    flex-direction: column;
}
`

const ContactForm = styled.div`
display: flex;
flex-direction: column;
justify-content: center;
width: 50%;   
margin: 30px;
padding: 30px;
    div {
        padding: 5px;
        display: flex;
        flex-direction: column; 
        align-items: center;
        justify-content: center;
    }

    input {
        background-color: #3D2A35;
        color: #FFB7CB;
        width: 300px;
        margin: 5px;
        border-radius: 5px;
        height: 22px;
        font-family: 'Questrial', sans-serif;
        font-size: 16px;

        &:focus {
            border: 1px solid #D70170;
        }
    }

    textarea {
        background-color: #3D2A35;
        color: #FFB7CB;
        height: 200px;
        resize: none;   
        width: 300px;
        border-radius: 5px;
        font-family: 'Questrial', sans-serif;
        margin-top: 10px;
        font-size: 16px;
    }

    input[type=submit] {
        padding: 5px;
        margin-top: 15px;
        background-color: #D70170;
        font-family: 'Questrial', sans-serif;
        cursor: pointer;
        font-size: 1.1rem;
        border-radius: 10px;
        height: 30px;
        font-weight: 600;
        color: #FFB7CB;

        &:hover {
            background-color: #FFB7CB; 
            color: #D70170;
        }
    }
@media (max-width: 700px) {
    margin: 10px;
    padding: 5px;
    width: 90%;
}
`

const ContactInfo = styled.div`
width: 50%;
font-family: 'Questrial', sans-serif;
padding: 30px;
margin: 30px;
hr {
    color: #D70170;
    background-color: #D70170;
    border-width: 0px;
    height: 2px;
}

@media (max-width: 700px) {
    padding: 0px;
    width: 80%;
}

`

const ContactLoc = styled.div`
text-align: center;

div {
    font-size: 18px;
    padding: 2px;
}
`

const ContactSocials = styled.div`
display: flex;
align-items: center;
justify-content: center;
`

const ContactSection = () => {

    function sendEmail(e) {
        e.preventDefault();

        emailjs.sendForm('service_hu0gre8', 'template_vg1ocdd', e.target, 'user_vBv77OvQZ0xnE7SqbMDZf')
        .then((result) => {
            alert(result.text);
        }, (error) => {
            alert(error.text);
        });
        e.target.reset()
    }
    return (
        <StyledContact>
            <ContactTitle>
                <h2>Contact</h2>
                <h4>I'd love to hear from you</h4>
            </ContactTitle>
            <ContactContent>
                
                <ContactInfo>
                    <ContactLoc>
                        <div>Wojciech Ratajczak</div>
                        <div>Kamienna 2, Wroc≈Çaw</div>
                        <div>509634294</div>
                        <div>wojciech.ratajczak92@gmail.com</div>
                    </ContactLoc>
                    <hr></hr>
                    <ContactSocials>
                        <Socialls/>
                    </ContactSocials>
                </ContactInfo>
                <ContactForm>
                    <form onSubmit={sendEmail}>
                    <div>
                        <input type="text" name="name" placeholder="Your name.." required/>
                        <input type="email" name="email" placeholder="Your email.." required />
                        <input type="text" name="subject" placeholder="Subject.." required/>
                        <textarea name="message" placeholder="Write something" required/>
                        <input type="submit" value="Send" />
                    </div>
                </form>
                </ContactForm>
            </ContactContent>
        </StyledContact>
    )
}

export default ContactSection
